
import moment from "moment";
import Remote from "@/remote/scheduleRequest";
import ScheduleDetail from "@/components/scheduleDetail/ScheduleDetail.vue";
import SearchSchedule from "@/components/schedulesManagement/SearchSchedule.vue";
import Dialog from "@/components/dialog";
import { defineComponent } from "vue";
import { EmployeeActions } from "@/store/employeeModule";
declare var Affinity: any;

export default defineComponent({
    name: "schedulesManagement",
    components: {
        SearchSchedule,
        "schedule-detail": ScheduleDetail,
    },
    data() {
        return {
            selectedSched: <any>null,
            shiftTypeList: [],
            schedDetail: <any>null,
            createNew: false,
            empListHeight: "",
            showActiveSchedule: true,
            employees: [],
        };
    },
    async mounted() {
        Dialog.loading();
        await Promise.all([this.getShiftTypeList(), this.getEmployees()]);
        Dialog.clear();
    },
    methods: {
        async getShiftTypeList() {
            try {
                let shiftList = await Remote.ShiftTypes();
                this.shiftTypeList = shiftList.map((s: any) => s.ShiftType);
            } catch (e) {
                console.error(e);
            }
        },

        async getEmployees() {
            try {
                this.employees = await this.$store.dispatch(
                    EmployeeActions.getEmployees,
                    Affinity.user.EmployeeNo
                );
            } catch (e) {
                console.error(e);
            }
        },

        toggleShowActiveSchedule() {
            this.showActiveSchedule = !this.showActiveSchedule;
        },

        selectSchedulesDetail(sched: any) {
            if (this.$refs.detail && (this.$refs as any).detail.hasChanges) {
                Dialog.checkChange({
                    message:
                        "There are currently Schedule Detail changes <br /> Do you want to discard your changes?",
                    onOk: () => {
                        this.loadScheduleDetail(sched);
                    },
                });
            } else {
                this.loadScheduleDetail(sched);
            }
        },

        loadScheduleDetail(sched: any) {
            Dialog.loading();
            Remote.ScheduleDetail(sched.Id).then(
                (res) => {
                    this.schedDetail = res.Data;
                    this.checkEmpAllocationActive();
                    Dialog.clear();
                    this.selectedSched = sched;
                },
                (err) => {
                    Dialog.clear();
                    if (err.message === "Network Error") {
                        Dialog.alert(
                            "Lost Internet connection. Please try again after reconnect."
                        );
                    } else {
                        Dialog.alert("Something went wrong");
                    }
                }
            );
        },

        checkEmpAllocationActive() {
            this.schedDetail.EmpAllocations.forEach((detail: any) => {
                if (!detail.DateTo) {
                    detail.isActive = true;
                    return;
                }
                let dateFrom = moment(detail.DateFrom);
                let dateTo = moment(detail.DateTo);
                detail.isActive =
                    dateTo.diff(moment(), "day") >= 0 &&
                    dateFrom.diff(moment(), "day") <= 0;
            });
        },

        createNewSchedule: function () {
            this.createNew = true;
            let startDate = moment();
            startDate.hour(0);
            startDate.minute(0);
            startDate.second(0);
            this.schedDetail = {
                Id: -1,
                Name: "",
                Description: "",
                NoOfWeeks: 1,
                StartDate: startDate,
                Days: [],
            };

            if (this.$refs.detail) {
                (this.$refs.detail as any).hasChanges = true;
            }
        },

        cancelScheduleDetail() {
            if (this.createNew) {
                this.createNew = false;
            }

            this.schedDetail = null;
        },

        reloadSchedulesDetail: function () {
            if (!this.selectedSched) this.schedDetail = null;
            else this.selectSchedulesDetail(this.selectedSched);

            this.createNew = false;
        },

        closeScheduleDetail(changeCheck: boolean) {
            if (!changeCheck || !(this.$refs as any).detail.hasChanges) {
                this.schedDetail = null;
                this.createNew = false;
                this.selectedSched = null;
                (this.$refs as any).searchScheduleRef.deselectScheDetail();
                return;
            }

            Dialog.checkChange({
                message:
                    "There are currently Schedule Detail changes <br /> Do you want to discard your changes?",
                onOk: () => {
                    this.closeScheduleDetail(false);
                },
            });
        },

        scheduleSaved: function (detail: any) {
            if (!this.selectedSched) this.schedDetail = null;
            else {
                this.schedDetail = detail;
                this.selectedSched = {
                    Id: detail.Id,
                };
            }

            (this.$refs as any).searchScheduleRef.getSchedulesList();
            this.createNew = false;
        },

        scheduleDeleted: function () {
            this.selectedSched = null;
            this.schedDetail = null;
            (this.$refs as any).searchScheduleRef.getSchedulesList();
        },
    },
});
