
import { defineComponent, onMounted, ref } from "vue";
import moment from "moment";
import Remote from "@/remote/scheduleRequest";
import Dialog from "@/components/dialog";
import Paginator from "@/components/Analysis/Paginator.vue";

declare var Affinity: any;

export default defineComponent({
    name: "CostAnalysisV2",
    components: {
        Paginator,
    },
    props: {
        fromDate: {
            type: Object,
        },
        toDate: {
            type: Object,
        },
        employeeList: {
            type: Array,
        },
    },

    setup(props: any) {
        const data = ref(<any[]>[]);
        const pageNumber = ref(1);
        const pageSize = ref(20);
        const totalPages = ref(5);
        const recordCount = ref(0);
        const noDataError = ref(false);
        const PaginatorRef = ref<InstanceType<typeof Paginator>>();

        const dataHeadings = ref([
            "Employee Name",
            "Employee Number",
            "Functional Area",
            "Date",
            "Award",
            "Scheduled Hours",
            "Actual Hours",
            "Hours Variance",
            "Scheduled Cost",
            "Actual Cost",
            "Cost Variance",
            "Actual Start Time",
            "Actual End Time",
            "Adjusted Start Time",
            "Adjusted End Time",
        ]);

        const dataAnalysis = ref(<any[]>[]);
        const options = ref(["Start/End Time", "Detailed Cost Report"]);

        onMounted(() => {
            dataAnalysis.value = [];
            pageNumber.value = 1;
            totalPages.value = 5;
            noDataError.value = false;
        });

        async function loadGridDataFromParent() {
            resetData();
            Dialog.loading();
            await loadGridData();
            Dialog.clear();
        }

        async function loadGridData() {
            noDataError.value = false;

            if (!props.fromDate || !props.toDate) {
                if (!props.fromDate) {
                    props.fromDate = moment().startOf("month");
                }
                if (!props.toDate) {
                    props.toDate = moment().endOf("month");
                }
            }

            const query = {
                pageNumber: pageNumber.value,
                pageSize: pageSize.value,
                pagingEnabled: true,
                sortBy: "CommonName",
                sortingType: "asc",
                employeeNumbers: props.employeeList,
                transDateFrom: props.fromDate
                    ? props.fromDate.format("YYYY-MM-DD")
                    : null,
                transDateTo: props.toDate
                    ? props.toDate.format("YYYY-MM-DD")
                    : null,
            };

            await Remote.GetCostAnalysisV2(query).then((res: any) => {
                if (
                    (res.response && res.response.length === 0) ||
                    !res.response
                ) {
                    noDataError.value = true;
                    return;
                }

                dataAnalysis.value = res.response.map((e: any) => {
                    const a: any = {};
                    a.commonName = e.commonName;
                    a.employeeNo = e.employeeNo;
                    a.functionalArea = e.functionalArea;
                    a.transDate = moment(e.transDate).format("YYYY-MM-DD");
                    a.awardId = e.awardId;
                    a.scheduledHours = e.scheduledUnits;
                    a.actualHours = e.actualUnits;
                    a.hoursVariance =
                        Math.round((a.actualHours - a.scheduledHours) * 100) /
                        100;
                    a.scheduledCost = e.scheduledCost
                        ? Math.round(e.scheduledCost * 100) / 100
                        : null;
                    a.actualCost = e.actualCost
                        ? Math.round(e.actualCost * 100) / 100
                        : null;
                    a.costVariance =
                        Math.round((a.actualCost - a.scheduledCost) * 100) /
                        100;
                    a.actStartTime = e.actStartTime
                        ? moment(e.actStartTime).format("HH:mm")
                        : null;
                    a.actEndTime = e.actEndTime
                        ? moment(e.actEndTime).format("HH:mm")
                        : null;
                    a.adjStartTime = e.adjStartTime
                        ? moment(e.adjStartTime).format("HH:mm")
                        : null;
                    a.adjEndTime = e.adjEndTime
                        ? moment(e.adjEndTime).format("HH:mm")
                        : null;

                    return a;
                });
                totalPages.value = res.totalPages;
                pageNumber.value = res.currentPageNumber;
                recordCount.value = res.recordCount;

                PaginatorRef.value!.calculatePageList(
                    res.currentPageNumber,
                    res.totalPages
                );
                return res.response;
            });
        }

        async function selectPage(newPageNumber: number) {
            pageNumber.value = newPageNumber;
            Dialog.loading();
            await loadGridData();
            Dialog.clear();
        }

        async function exportCSV() {
            if (noDataError.value) {
                Dialog.alert("No data to export");
                return;
            }

            Dialog.loading();
            const allData: any = await loadAllData();
            Dialog.clear();
            let csvContent = "data:text/csv;charset=utf-8,";

            csvContent += dataHeadings.value.join(",") + "\r\n";

            allData.map((rowArray: any) => {
                csvContent += Object.values(rowArray).join(",") + "\r\n";
            });

            var encodedUri = encodeURI(csvContent);
            var link = document.createElement("a");
            link.setAttribute("href", encodedUri);
            link.setAttribute("download", "Analytic.csv");
            document.body.appendChild(link); // Required for FF

            link.click();
        }

        async function loadAllData() {
            const query = {
                pageNumber: 1,
                pageSize: 10000,
                pagingEnabled: false,
                sortBy: "CommonName",
                sortingType: "asc",
                employeeNumbers: props.employeeList,
                transDateFrom: props.fromDate
                    ? props.fromDate.format("YYYY-MM-DD")
                    : null,
                transDateTo: props.toDate
                    ? props.toDate.format("YYYY-MM-DD")
                    : null,
            };
            return await Remote.GetCostAnalysisV2(query).then((res: any) => {
                return res.response.map((e: any) => {
                    const a: any = {};
                    a.commonName = e.commonName;
                    a.employeeNo = e.employeeNo;
                    a.functionalArea = e.functionalArea;
                    a.transDate = moment(e.transDate).format("YYYY-MM-DD");
                    a.awardId = e.awardId;
                    a.scheduledHours = e.scheduledUnits;
                    a.actualHours = e.actualUnits;
                    a.hoursVariance =
                        Math.round((a.actualHours - a.scheduledHours) * 100) /
                        100;
                    a.scheduledCost = e.scheduledCost
                        ? Math.round(e.scheduledCost * 100) / 100
                        : null;
                    a.actualCost = e.actualCost
                        ? Math.round(e.actualCost * 100) / 100
                        : null;
                    a.costVariance =
                        Math.round((a.actualCost - a.scheduledCost) * 100) /
                        100;
                    a.actStartTime = e.actStartTime
                        ? moment(e.actStartTime).format("HH:mm")
                        : null;
                    a.actEndTime = e.actEndTime
                        ? moment(e.actEndTime).format("HH:mm")
                        : null;
                    a.adjStartTime = e.adjStartTime
                        ? moment(e.adjStartTime).format("HH:mm")
                        : null;
                    a.adjEndTime = e.adjEndTime
                        ? moment(e.adjEndTime).format("HH:mm")
                        : null;

                    return a;
                });
            });
        }

        function resetData() {
            pageNumber.value = 1;
            totalPages.value = 5;
            noDataError.value = false;
        }

        return {
            data,
            dataHeadings,
            dataAnalysis,
            options,
            PaginatorRef,
            pageNumber,
            pageSize,
            totalPages,
            noDataError,
            recordCount,
            loadGridData,
            exportCSV,
            selectPage,
            loadGridDataFromParent,
        };
    },
});
