import {Moment} from "moment";

export function getDialogDate(calendarFocusDate: Moment, dayInWeek: number): any {
    if (window.innerWidth < 1010) {
       return calendarFocusDate.format('DD/MM/YYYY')
    }
    return calendarFocusDate.day(dayInWeek).format('DD/MM/YYYY')
}

export function getDialogDateForRequest(calendarFocusDate: Moment, dayInWeek: number): any {
    if (window.innerWidth < 1010) {
        return calendarFocusDate.format('MM/DD/YYYY')
    }
    return calendarFocusDate.day(dayInWeek).format('MM/DD/YYYY')
}
