
import { emitter } from "../../main";
import Remote from "@/remote/scheduleRequest";
import EmpScheduleDetail from "@/components/employeeAllocation/EmpSchedDetail.vue";
import Dialog from "@/components/dialog";
import { defineComponent, ref } from "vue";

export default defineComponent({
    name: "employeeSchedule",
    components: { EmpScheduleDetail },
    props: ["employee"],
    data: function () {
        return {
            selectedEmp: <any>null,
            empSchedList: <any[]>[],
            activeOnly: false,
            selectedEmpSched: <any>null,
        };
    },
    setup(props) {
        const selectedEmp = ref<any>(props.employee);

        return { selectedEmp };
    },
    mounted() {
        this.selectedEmp = this.employee;
        if (this.selectedEmp && this.selectedEmp.EmployeeNo) {
            this.getEmpWorkScheds();
        } else {
            console.error("employee or employee number missing");
        }
    },
    watch: {
        employee() {
            this.selectedEmp = this.employee;
            if (this.selectedEmp && this.selectedEmp.EmployeeNo) {
                this.getEmpWorkScheds();
            } else {
                console.error("employee or employee number missing");
            }
        },
    },
    methods: {
        selectEmpSchedCheck(empSched: any) {
            if (
                this.$refs.empScheDetailRef &&
                (this.$refs.empScheDetailRef as any).hasChanges
            ) {
                Dialog.checkChange({
                    message:
                        "There are currently Schedule changes <br /> Do you want to discard your changes?",
                    onOk: () => {
                        this.selectEmpSched(empSched);
                    },
                });
            } else {
                this.selectEmpSched(empSched);
            }
        },
        selectEmpSched(empSched: any) {
            this.empSchedList.forEach((empSched) => {
                empSched.classObject.selected = false;
            });

            empSched.classObject.selected = true;
            this.selectedEmpSched = empSched;
        },

        getEmpWorkScheds() {
            Dialog.loading();
            Remote.EmpWorkScheds(
                this.selectedEmp.EmployeeNo +
                    (this.activeOnly ? "?activeOnly=true" : "?activeOnly=false")
            ).then((res) => {
                res.Data.map((empSched: any, index: number) => {
                    empSched.classObject = {
                        selected: false,
                        odd: index % 2,
                    };
                    empSched.DateFrom = new Date(Date.parse(empSched.DateFrom));
                    if (empSched.DateTo)
                        empSched.DateTo = new Date(Date.parse(empSched.DateTo));
                });
                this.empSchedList = res.Data;
                Dialog.clear();
            });
        },

        updateActiveOnly() {
            this.getEmpWorkScheds();
        },
        closeEmployeeSchedule(changeCheck: any) {
            if (
                !changeCheck ||
                !this.$refs.empScheDetailRef ||
                (this.$refs.empScheDetailRef &&
                    !(this.$refs.empScheDetailRef as any).hasChanges)
            ) {
                this.$emit("close");
                return;
            }

            Dialog.checkChange({
                message:
                    "There are currently Schedule changes <br /> Do you want to discard your changes?",
                onOk: () => {
                    this.closeEmployeeSchedule(false);
                },
            });
        },
        deleteSelection() {
            this.selectedEmpSched = null;
            this.getEmpWorkScheds();
        },
        updatedEmpSchedule() {
            this.selectedEmpSched = null;
            this.getEmpWorkScheds();
        },
    },
});
