
import { useStore } from "vuex";
import { computed, defineComponent } from "vue";
import {
    FlexScheduleDay,
    FlexScheduleMutation,
    UnAvailability,
} from "@/store/flexScheduleModule";
import Modal from "@/components/common/Modal.vue";
// import {key} from "@/store";
import Remote from "@/remote/scheduleRequest";
import { CostElement, EMPPosition, ShiftType } from "@/store/lookupModule";
import CustomSelect from "@/components/common/CustomSelect.vue";
import { WeeklyPublishStatus } from "@/components/FlexSchedule/FlexSchedule.vue";
import Dialog from "@/components/dialog";
import moment, { Moment } from "moment";

import {
    getDialogDate,
    getDialogDateForRequest,
} from "@/components/FlexSchedule/dialogHelper";
import { isMobile } from "@/components/common/script";

export default defineComponent({
    name: "EditFlexSchedule",
    components: {
        Modal,
        CustomSelect,
    },
    props: {
        isEdit: {
            type: Boolean,
        },
        weekStatus: {
            type: String,
        },
    },
    data() {
        return {
            day: <FlexScheduleDay>{}, // main form json

            unAvailability: <UnAvailability | null>null,
            selectedPosition: <EMPPosition>{},
            selectedShift: <ShiftType>{},
            selectedCostElement1: <CostElement>{},
            selectedCostElement2: <CostElement>{},
            employeeDetail: <any>{},
            dialogDate: <string>"",
            showUnAvaError: false,
            showTimeHourError: false,
            showTimeMinuteError: false,

            breakHours: <null | number>null,
            breakMinutes: <null | number>null,
        };
    },
    setup() {
        // console.log('setup');
        const store = useStore();
        // const flexScheduleModule = getModule
        // const state = reactive({
        //   day: computed(() => store.state.flexSchedule.flexScheduleDay),
        // });
        //
        // console.log('state', state)
        // return {state}
        return {
            // day: computed(() => {
            //     console.log('test', store.state)
            //     return store.state.FlexSchedule.flexScheduleDay
            // }),

            shiftOptions: computed(() => {
                let blankOption = {
                    shiftType: "",
                    description: "",
                    isDefault: null,
                    archive: "F",
                };
                let shiftOption = store.state.LookUp.shifts.slice();
                shiftOption.unshift(blankOption);
                return shiftOption;
                // return store.state.LookUp.shifts
            }),
            costElementOptions: computed(() => {
                return store.state.LookUp.costElements;
            }),
        };
    },
    computed: {
        positionOptions(): any {
            let options = this.$store.state.LookUp.positions;
            if (options.length === 1) {
                (this as any).selectedPosition = options[0];
            }
            return options;
        },
        fixedHours: {
            get(): string {
                return this.day.hours ? this.day.hours.toFixed(2) : "";
            },
            set(fixedHours: string) {
                this.day.hours = Number(fixedHours);
            },
        },
        fixedBreakLength: {
            get(): string {
                return this.day.breakLength
                    ? this.day.breakLength.toFixed(2)
                    : "";
            },
            set(breakLengthString: string) {
                this.day.breakLength = Number(breakLengthString);
            },
        },
    },
    // computed: mapState([
    //   // map this.count to store.state.count
    //   'flexSchedule/startTime'
    // ]),
    mounted() {
        // let startTime = this.$store.state.FlexScheduleModule.startTime;
        let startTime = this.$store;
        this.day = this.$store.state.FlexSchedule.flexScheduleDay;
    },

    // computed: {
    //   ...mapState({
    //     day: (state: State) => state.flexSchedule.flexScheduleDay
    //   }),
    // },

    methods: {
        selectCostElement1(costElement1: any) {
            this.selectedCostElement1 = costElement1;
        },
        selectCostElement2(costElement2: any) {
            this.selectedCostElement2 = costElement2;
        },
        selectShift(shift: any) {
            this.selectedShift = shift;
        },
        selectPositionCustom(position: any) {
            this.selectedPosition = position;
        },

        selectPosition($event: any, position: EMPPosition) {
            this.selectedPosition = position;
            $event.target.blur();
            let b = this.$refs.selectRef as any;
            b.blur();
        },

        updateStartTime(day: FlexScheduleDay) {
            if (!day.startTime) {
                return;
            }

            let startTime;
            if (day.startTime.includes(":")) {
                startTime = moment(day.startTime, "HH:mm");
            } else {
                startTime = moment(day.startTime, "Hmm");
            }

            if (startTime.isValid()) {
                day.startTime = startTime.format("HH:mm");
                this.calcNetHour();
            } else {
                // todo: display this in error message
                day.startTime = "Invalid Time";
            }
        },

        updateEndTime(day: FlexScheduleDay) {
            if (!day.endTime) {
                return;
            }

            let endTime;
            if (day.endTime.includes(":")) {
                endTime = moment(day.endTime, "HH:mm");
            } else {
                endTime = moment(day.endTime, "Hmm");
            }

            if (endTime.isValid()) {
                day.endTime = endTime.format("HH:mm");
                this.calcNetHour();
            } else {
                // todo: display this in error message
                day.endTime = "Invalid Time";
            }
        },

        breakValidation(e: any) {
            if (e.key.match(/[-]/)) {
                e.preventDefault();
            }
        },

        checkBreakHours() {
            if (this.day.breakLength) {
                let n = this.day.breakLength;
                let hourU = Math.floor(n);
                let minU = n - hourU;

                if (hourU !== 0) {
                    this.breakHours = hourU;
                }

                if (minU > 0) {
                    this.breakMinutes = Math.round(minU * 60);
                }
            }
        },

        calcBreakHour() {
            if (this.breakHours && this.breakMinutes) {
                this.day.breakLength =
                    Number(this.breakHours) +
                    Math.round((Number(this.breakMinutes) / 60) * 1000) / 1000;
            } else if (this.breakHours) {
                this.day.breakLength = Number(this.breakHours);
            } else if (this.breakMinutes) {
                this.day.breakLength =
                    Math.round((Number(this.breakMinutes) / 60) * 1000) / 1000;
            } else if (this.breakHours === 0 && this.breakMinutes === 0) {
                this.day.breakLength = 0;
            } else {
                // otherwise assume value deleted.
                this.day.breakLength = 0;
            }

            this.calcNetHour();
        },

        calcNetHour() {
            if (
                this.day.startTime === "Invalid Time" ||
                this.day.endTime === "Invalid Time"
            ) {
                // Invalid time in form, do not calculate
                return;
            }

            if (!this.day.startTime || !this.day.endTime) {
                // Missing value, do not calculate
                return;
            }

            let startTime = moment(this.day.startTime, "H:mm");
            let endTime = moment(this.day.endTime, "H:mm");

            let startEndDiffTrue = endTime.diff(startTime, "hours", true);

            // if endTime is before startTime, indicate user input a next day value.
            // add one day and then calculate again for net hour
            // this case count as start day's schedule (not two schedule)
            if (startEndDiffTrue <= 0) {
                startEndDiffTrue = endTime
                    .add(1, "day")
                    .diff(startTime, "hours", true);
            }

            let startEndDiff = Math.round(startEndDiffTrue * 100) / 100;

            if (this.day.breakLength) {
                this.day.hours = startEndDiff - this.day.breakLength;
            } else {
                this.day.hours = startEndDiff;
            }

            this.checkUnavailability();
        },

        checkUnavailability() {
            this.showUnAvaError = false;

            if (!this.day.startTime || !this.day.endTime) {
                return;
            }

            if (
                !this.unAvailability ||
                !this.unAvailability.endTime ||
                !this.unAvailability.startTime
            ) {
                return;
            }

            const startTime = moment(this.day.startTime, "HH:mm");
            const endTime = moment(this.day.endTime, "HH:mm");
            const unAvaStartTime = moment(
                this.unAvailability.startTime,
                "HH:mm"
            );
            const unAvaEndTime = moment(this.unAvailability.endTime, "HH:mm");

            // Fine time overlap
            // (StartA <= EndB) and (EndA >= StartB)
            if (startTime <= unAvaEndTime && endTime >= unAvaStartTime) {
                console.log("show error");
                this.showUnAvaError = true;
            }
        },

        clearForm() {
            this.$store.commit(FlexScheduleMutation.clearForm);
            this.showUnAvaError = false;
            this.showTimeHourError = false;
            this.showTimeMinuteError = false;
        },

        cancel() {
            this.$store.commit(FlexScheduleMutation.clearForm);
            this.showUnAvaError = false;
            this.showTimeHourError = false;
            this.showTimeMinuteError = false;
            this.toggleShow();
        },

        getPublishStateBaseOnEmp() {
            if (isMobile()) {
                return "draft";
            }

            const empNumber =
                this.$store.state.FlexSchedule.selectedFlexDay.employeeNo;
            const empPublishState =
                this.$store.state.FlexSchedule.scheduleWeek.get(
                    empNumber
                ).empSchePublishState;

            if (empPublishState === "published") {
                return "published";
            } else {
                return "draft";
            }
        },

        validateForm() {
            this.showTimeHourError = false;
            this.showTimeMinuteError = false;
            if (
                this.breakHours &&
                (this.breakHours as any).toString().indexOf(".") > -1
            ) {
                this.showTimeHourError = true;
                return false;
            } else if (
                this.breakMinutes &&
                (this.breakMinutes as any).toString().indexOf(".") > -1
            ) {
                this.showTimeMinuteError = true;
                return false;
            } else {
                return true;
            }
        },

        updateSche() {
            if (!this.validateForm()) {
                return;
            }
            const date = this.$store.state.FlexSchedule.calendarFocusDate
                .day(this.$store.state.FlexSchedule.selectedFlexDay.dayInWeek)
                .format("MM/DD/YYYY");
            const [startTime, endTime] = this.getStartAndEndTimeWithDate(
                date,
                this.day.startTime,
                this.day.endTime
            );

            Dialog.loading();
            let putData = {
                Id: this.$store.state.FlexSchedule.selectedFlexDay.id,
                EmployeeNo:
                    this.$store.state.FlexSchedule.selectedFlexDay.employeeNo,
                Date: date,
                StartTime: startTime,
                EndTime: endTime,
                Hours: this.day.hours,
                PublishStatus: this.$store.state.FlexSchedule.selectedFlexDay
                    .publishStatus
                    ? this.$store.state.FlexSchedule.selectedFlexDay
                          .publishStatus
                    : this.getPublishStateBaseOnEmp(),
                ScheduleType: "Flex",
                ScheduleData: {
                    ShiftId: this.selectedShift.shiftType,
                    BreakLength: this.day.breakLength,
                    Position: this.selectedPosition.positionCode,
                    CostElement1: this.selectedCostElement1
                        ? this.selectedCostElement1.id
                        : undefined,
                    CostElement2: this.selectedCostElement1
                        ? this.selectedCostElement2.id
                        : undefined,
                },
            };

            Remote.PatchFlexSchedule([putData]).then(
                (res) => {
                    this.$emit("reload");
                    Dialog.clear();
                },
                () => {
                    Dialog.clear();
                    Dialog.alert(
                        "Something went wrong. Update Schedule failed"
                    );
                }
            );

            this.$store.commit(FlexScheduleMutation.saveScheduleDayToWeek);
            this.toggleShow();
        },

        createSche() {
            if (!this.validateForm()) {
                return;
            }

            const date = getDialogDateForRequest(
                this.$store.state.FlexSchedule.calendarFocusDate,
                this.$store.state.FlexSchedule.selectedFlexDay.dayInWeek
            );
            const [startTime, endTime] = this.getStartAndEndTimeWithDate(
                date,
                this.day.startTime,
                this.day.endTime
            );

            Dialog.loading();
            let postData = [
                {
                    EmployeeNo:
                        this.$store.state.FlexSchedule.selectedFlexDay
                            .employeeNo,
                    Date: date,
                    StartTime: startTime,
                    EndTime: endTime,
                    Hours: this.day.hours,
                    PublishStatus: this.$store.state.FlexSchedule
                        .selectedFlexDay.publishStatus
                        ? this.$store.state.FlexSchedule.selectedFlexDay
                              .publishStatus
                        : this.getPublishStateBaseOnEmp(),
                    ScheduleType: "Flex",
                    ScheduleData: {
                        ShiftId: this.selectedShift.shiftType
                            ? this.selectedShift.shiftType
                            : "",
                        BreakLength: this.day.breakLength,
                        Position: this.selectedPosition.positionCode,
                        CostElement1: this.selectedCostElement1
                            ? this.selectedCostElement1.id
                            : "",
                        CostElement2: this.selectedCostElement2
                            ? this.selectedCostElement2.id
                            : "",
                    },
                },
            ];
            //
            Remote.CreateFlexSchedule(postData)
                .then((res) => {
                    //todo what happen after create
                    // console.log('create res', res)
                    // if (res === "Ok") {
                    if (res.Error) {
                        throw res;
                    }
                    this.$store.commit(
                        FlexScheduleMutation.saveScheduleDayToWeek
                    );
                    Dialog.clear();
                    this.toggleShow();
                    this.$emit("reload");
                })
                .catch((err) => {
                    Dialog.clear();
                    if (err.Error) {
                        Dialog.alert(err.Error);
                    } else {
                        Dialog.alert(
                            "Something went wrong. Create Schedule failed"
                        );
                    }
                });
        },

        allowDelete() {
            return this.$store.state.FlexSchedule.selectedFlexDay.id !== 0;
        },

        confirmIfDeleteSche() {
            if (!this.$store.state.FlexSchedule.selectedFlexDay.id) {
                Dialog.alert(
                    "This record cannot be deleted yet, as it is too far away from today's date."
                );
                return;
            }

            Dialog.confirmOrCancel({
                message: "Are you sure you want to delete this schedule?",
            }).then(() => {
                this.deleteSche(
                    this.$store.state.FlexSchedule.selectedFlexDay.id
                );
            });
        },

        deleteSche(id: number) {
            Dialog.loading();
            const date = getDialogDateForRequest(
                this.$store.state.FlexSchedule.calendarFocusDate,
                this.$store.state.FlexSchedule.selectedFlexDay.dayInWeek
            );
            const [startTime, endTime] = this.getStartAndEndTimeWithDate(
                date,
                this.day.startTime,
                this.day.endTime
            );
            let deleteData = {
                Id: this.$store.state.FlexSchedule.selectedFlexDay.id,
                EmployeeNo:
                    this.$store.state.FlexSchedule.selectedFlexDay.employeeNo,
                Date: date,
                StartTime: startTime,
                EndTime: endTime,
                Hours: this.day.hours,
                PublishStatus: this.$store.state.FlexSchedule.selectedFlexDay
                    .publishStatus
                    ? this.$store.state.FlexSchedule.selectedFlexDay
                          .publishStatus
                    : "draft",
                ScheduleData: {
                    ShiftId: this.selectedShift.shiftType,
                    BreakLength: this.day.breakLength,
                    Position: this.selectedPosition.positionCode,
                    CostElement1: this.selectedCostElement1
                        ? this.selectedCostElement1.id
                        : undefined,
                    CostElement2: this.selectedCostElement1
                        ? this.selectedCostElement2.id
                        : undefined,
                },
            };
            Remote.DeleteFlexSchedule(deleteData)
                .then(
                    (res) => {
                        //todo what happen after delete
                        //console.log('res', res)

                        // if (res === "Deleted") {
                        this.$emit("reload");

                        this.toggleShow();
                        // } else {
                        //     Dialog.alert('Something went wrong. Delete Schedule failed')
                        // }
                    },
                    (err) => {
                        Dialog.alert(
                            "Something went wrong. Delete Schedule failed"
                        );
                    }
                )
                .finally(() => {
                    // Dialog.clear();
                });
        },

        getStartAndEndTimeWithDate(
            date: string,
            startTime: string,
            endTime: string
        ) {
            if (!startTime && endTime) {
                const dateM = moment(date);
                const endTimeToday = moment(endTime, "HH:mm");
                const endTimeM = dateM
                    .clone()
                    .hours(endTimeToday.hour())
                    .minutes(endTimeToday.minutes());
                return [null, endTimeM];
            } else if (startTime && !endTime) {
                const dateM = moment(date);
                const startTimeToday = moment(startTime, "HH:mm");
                const startTimeM = dateM
                    .clone()
                    .hours(startTimeToday.hour())
                    .minutes(startTimeToday.minutes());
                return [startTimeM, null];
            } else if (!startTime && !endTime) {
                return [null, null];
            } else {
                const dateM = moment(date);
                const startTimeToday = moment(startTime, "HH:mm");
                const endTimeToday = moment(endTime, "HH:mm");
                const startTimeM = dateM
                    .clone()
                    .hours(startTimeToday.hour())
                    .minutes(startTimeToday.minutes());
                const endTimeM = dateM
                    .clone()
                    .hours(endTimeToday.hour())
                    .minutes(endTimeToday.minutes());

                let startEndDiffTrue = endTimeM.diff(startTimeM, "hours", true);

                if (startEndDiffTrue <= 0) {
                    endTimeM.add(1, "day");
                }

                return [
                    startTimeM.format("YYYY-MM-DDTHH:mm:ss"),
                    endTimeM.format("YYYY-MM-DDTHH:mm:ss"),
                ];
            }
        },

        openEdit() {
            // clean
            this.breakMinutes = null;
            this.breakHours = null;

            // todo load position

            // todo load shift
            let selectedShift;
            if (this.$store.state.FlexSchedule.selectedFlexDay.ScheduleData) {
                selectedShift = this.shiftOptions.find((option: ShiftType) => {
                    return (
                        option.shiftType ===
                        this.$store.state.FlexSchedule.selectedFlexDay
                            .ScheduleData.ShiftId
                    );
                });
            }

            if (selectedShift) {
                this.selectedShift = selectedShift;
            }

            // todo load custom element

            if (this.$store.state.FlexSchedule.selectedFlexDay.ScheduleData) {
                this.selectedCostElement1 = this.costElementOptions.find(
                    (option: CostElement) => {
                        return (
                            option.id ===
                            this.$store.state.FlexSchedule.selectedFlexDay
                                .ScheduleData.CostElement1
                        );
                    }
                );

                this.selectedCostElement2 = this.costElementOptions.find(
                    (option: CostElement) => {
                        return (
                            option.id ===
                            this.$store.state.FlexSchedule.selectedFlexDay
                                .ScheduleData.CostElement2
                        );
                    }
                );
            }

            this.employeeDetail = {
                employeeName:
                    this.$store.state.FlexSchedule.selectedFlexDay.employeeName,
                employeeNo:
                    this.$store.state.FlexSchedule.selectedFlexDay.employeeNo,
            };

            this.dialogDate = getDialogDate(
                this.$store.state.FlexSchedule.calendarFocusDate,
                this.$store.state.FlexSchedule.selectedFlexDay.dayInWeek
            );

            this.day = this.$store.state.FlexSchedule.flexScheduleDay;
            this.unAvailability =
                this.$store.state.FlexSchedule.selectedFlexDay?.unAvailability;
            this.checkUnavailability();
            this.checkBreakHours();
            (this.$refs.ModalRef as any).toggleShow();
        },

        toggleShow() {
            (this.$refs.ModalRef as any).toggleShow();
        },
    },
});
