
import {defineComponent, ref} from "vue";
import {useStore} from "vuex";
import {FlexScheduleCellSetting} from "@/store/flexScheduleModule";
import {nextTick} from "@vue/runtime-core";

export default defineComponent({
    name: "FlexScheduleCostCenter",

    props: {
        costCenterOptions: {
            type: Array,
        }
    },

    setup() {
        const store = useStore()
        const open = ref(false)
        const optionSelected = ref([])

        const costCenters = store.state.LookUp.costCenters;
        return {open, costCenters, optionSelected}
    },
    methods: {
        checkOptions(option: any) {
            // todo: trigger when user click on the row
            option.selected = !option.selected

            if (option.costCentreId === -1 && this.costCenterOptions) {
                for (const option of this.costCenterOptions) {
                    (option as any).selected = false;
                }

                (this.costCenterOptions as any)[0].selected = true;

            } else {
                (this.costCenterOptions as any)[0].selected = false;
            }

            this.$emit('selectOption', this.costCenterOptions)
        }
    }

})
