
import { defineComponent, ref } from "vue";
import _ from "lodash";

export default defineComponent({
    name: "AutoComplete",
    props: {
        inputList: {
            type: Array,
        },
        optionName: {
            type: String,
        },
    },
    data() {
        return {
            // selectSearchInput: false,
            editing: false,
            targetingIndex: <number | null>null,
            // downDownList: <any[]>[],
        };
    },
    setup(props: any) {
        if (!props.optionName) {
            // todo: treat as string array
            console.error("need object, optionName is missing");
        } else {
            // todo: treat as object array
            // console.log('inputLIst', props.inputList)
            // console.log('inputLIst', props.optionName)
        }

        const originList = ref(
            props.inputList.map((obj: any, index: number) => {
                return {
                    targeting: false,
                    name: obj[props.optionName],
                    index: index,
                };
            })
        );
        const dropDownList = ref(originList.value.slice());
        const selectSearchInput = ref("");

        return { originList, dropDownList, selectSearchInput };
    },

    methods: {
        updateSearchList(event: any) {
            if (event.key === "ArrowDown") {
                this.moveTargetDown();
                return;
            } else if (event.key === "ArrowUp") {
                this.moveTargetUp();
                return;
            } else if (event.key === "Enter") {
                if (this.targetingIndex) {
                    this.selectFromTarget();
                    return;
                }
            }

            this.dropDownList = this.originList.filter((o: any) => {
                o.targeting = false;
                return (
                    o.name
                        .toLowerCase()
                        .indexOf(this.selectSearchInput.toLowerCase()) > -1
                );
            });
        },

        moveTargetDown() {
            if (this.targetingIndex !== null) {
                this.dropDownList[this.targetingIndex].targeting = false;
                if (this.targetingIndex + 1 < this.dropDownList.length) {
                    this.targetingIndex++;
                    this.dropDownList[this.targetingIndex].targeting = true;
                } else if (
                    this.targetingIndex + 1 ===
                    this.dropDownList.length
                ) {
                    this.targetingIndex = 0;
                    this.dropDownList[this.targetingIndex].targeting = true;
                } else {
                    this.targetingIndex = 0;
                    this.dropDownList[this.targetingIndex].targeting = true;
                }
            } else {
                this.targetingIndex = 0;
                this.dropDownList[this.targetingIndex].targeting = true;
            }
        },

        moveTargetUp() {
            if (this.targetingIndex !== null) {
                this.dropDownList[this.targetingIndex].targeting = false;
                if (this.targetingIndex - 1 >= 0) {
                    this.targetingIndex--;
                    this.dropDownList[this.targetingIndex].targeting = true;
                } else if (this.targetingIndex - 1 < 0) {
                    this.targetingIndex = this.dropDownList.length - 1;
                    this.dropDownList[this.targetingIndex!].targeting = true;
                } else {
                    this.targetingIndex = 0;
                    this.dropDownList[this.targetingIndex].targeting = true;
                }
            } else {
                this.targetingIndex = 0;
                this.dropDownList[this.targetingIndex].targeting = true;
            }
        },

        selectFromTarget() {
            this.selectSearchInput =
                this.dropDownList[this.targetingIndex!].name;
            this.$emit("selected", this.inputList![this.targetingIndex!]);
        },

        chooseOption($event: any, option: any) {
            this.selectSearchInput = option.name;
            this.$emit("selected", this.inputList![option.index]);

            $event.target.blur();
        },
    },
});
