
import { defineComponent, ref } from "vue";

export default defineComponent({
    name: "CustomSelect",
    props: {
        options: {
            type: Array,
            default: [],
        },
        displayKey: {
            type: String,
            default: "",
        },
        modelValue: {
            type: [String, Number],
        },
        selected: {},
    },
    data: function () {
        return {
            left: 0,
            top: 0,
        };
    },
    mounted() {
        const rect = (this.$refs as any).selectRef.getClientRects()[0];
        this.left = rect.left;
        this.top = rect.top + rect.height;
    },
    methods: {
        selectOption($event: any, option: any) {
            $event.target.blur();
            let b = this.$refs.selectRef as any;
            b.blur();

            if (this.modelValue) {
                this.$emit("update:modelValue", option);
            } else {
                this.$emit("selectOption", option);
            }
        },
        setSelectItemsPosition() {
            const rect = (this.$refs as any).selectRef.getClientRects()[0];
            this.left = rect.left;
            this.top = rect.top + rect.height;
        },
    },
});
