
import {ScheduleDay} from "@/components/FlexSchedule/FlexScheduleInterface";
import {computed, defineComponent, PropType, reactive} from "vue";
import {FlexScheduleCellSetting, FlexScheduleMutation, ScheduleDayInView} from "@/store/flexScheduleModule";
import {useStore} from "vuex";
import {LookupActions, ShiftType} from "@/store/lookupModule";
import _ from "lodash";
import {isMobile} from "@/components/common/script";

export default defineComponent({
    name: "DayCell",

    props: {
        scheDay: {
            type: Object as PropType<ScheduleDay>
        },
        employeeNo: {
            type: Number
        },
        employeeName: {
            type: String
        }
    },

    data() {
        return {
            mobileShowCreate: false,
            mobileShowEdit: false,
        }
    },

    setup() {
        const store = useStore()

        const cellSettings: FlexScheduleCellSetting = store.state.FlexSchedule.flexScheduleCellSetting

        const shiftOptions = computed(() => {
            return store.state.LookUp.shifts
        })

        const positionOptions = computed(() => {
            return store.state.LookUp.positions
        })


        return {cellSettings,
            shiftOptions,
            positionOptions,
            selectedShift: computed((shiftId: string) => {
                return shiftOptions.value.find((option: ShiftType) => {
                    return option.shiftType === shiftId
                })
            }),
        }
    },
    methods: {
        moreDetail(day: ScheduleDay) {
            this.$store.dispatch(LookupActions.getPositions, this.employeeNo)
            const result = this.$store.state.FlexSchedule.flexScheduleRemoteData.find((data: any) => {
                return data.date === day.date && data.employeeNo === this.employeeNo
            })

            if (!result) {
                console.error('can not find selected schedule')
                // todo: add correct error message
                return;
            }

            //console.log('result', result)

            this.$store.commit(FlexScheduleMutation.setScheduleDay, result)
            this.$emit('openEditDialog', true)
        },

        createSche(scheDay: ScheduleDay) {
            // this.$store.commit(FlexScheduleMutation.setTargetEmployee)
            this.$store.dispatch(LookupActions.getPositions, this.employeeNo)
            let scheDayInView: any = _.clone(scheDay)
            scheDayInView.employeeNo = this.employeeNo;
            scheDayInView.employeeName = this.employeeName;
            this.$store.commit(FlexScheduleMutation.createNewScheduleDay, scheDayInView)
            this.$emit('openEditDialog', false)
        },
        shouldDisplayHours(scheDay: any) {
            if (!this.cellSettings.showHours.value) {
                return false;
            }

            if ( (scheDay.startTime || scheDay.endTime) && scheDay.hours === 0 ) {
                return true;
            }
            return !!scheDay.hours
        },

        isMobileG() {
            return isMobile();
        },

        clickDayCell() {
            this.mobileShowCreate = !this.mobileShowCreate;
            this.mobileShowEdit = !this.mobileShowEdit
        },

        checkMobileShowEdit() {
            if (isMobile()) {
                return this.mobileShowEdit
            } else {
                return true;
            }
        },

        checkMobileShowCreate() {
            if (isMobile()) {
                return this.mobileShowCreate
            } else {
                return true;
            }
        }
    }
})
